import React, {useState, useEffect} from 'react'
import axios from 'axios'
import { Link } from 'react-router-dom'


function AllEmail(props) {

    const [data, setData] = useState([])
    const [email_from, setEmail_from] = useState("")
    const [email_subject, setEmail_subject] = useState("")
    const [email_date, setEmail_date] = useState("")
    const [email_name, setEmail_name] = useState("")
    const [email_domain, setEmail_domain] = useState("")
    const [email, setEmail] = useState("")
    const [email_id, setEmail_id] = useState("")
    const [value, setValue] = useState(1)

    useEffect(async () => {
        try {
            console.log('props from allEmail', props);
            const propsEmail = props?.history?.location?.state?.mail
            if(propsEmail) {
                let name = propsEmail.split("@")[0]
                let domain = propsEmail.split("@")[1]
                setEmail(propsEmail);
                setEmail_name(name);
                setEmail_domain(domain)
                await fetchData();
            }
        }
        catch(e) {
            console.log('error in useEffect[] ', e);
        }
    },[])

    async function fetchData() {
        console.log('fetch Data');
        const response = await axios.get(`https://www.1secmail.com/api/v1/?action=getMessages&login=${email_name}&domain=${email_domain}`);
        console.log('respo', response);
        if(response.status == 200 && response?.data?.length > 0) {
            setData([...response?.data])
        }
    }

    const refreshMail = async () => {
        await fetchData()
    }

    return (
        <>
        {
            <div>
                <button onClick={refreshMail}> {"Refresh"} </button>
            </div>
        }
        {
            (data?.length > 0) ? (data.map((mailContent, index) => {
                return (
                    <div key={index}>
                    <Link to={{
                            pathname: "/contentPage",
                            state: { 
                                "email_id":mailContent?.id,
                                "email_domain":email_domain,
                                "email_name":email_name
                            }
                        }}
                        >
                        {index + " data " + JSON.stringify(mailContent)}
                    </Link>
                    </div>
                )
            })) :
            (
                <div>
                    {"No Emails"}
                </div>
            )
        }
        </>
    )
}

export default AllEmail;