import React, {useState, useEffect} from 'react'
import axios from 'axios'

function ContentPage(props) {
    
    const [data, setData] = useState('')
    
    useEffect(() => {
        try {
            const propsData = props?.history?.location?.state
            console.log('props from allEmail', propsData);
            const { email_domain, email_id, email_name } = propsData
            if(email_domain && email_id && email_name) {
                // let name = propsEmail.split("@")[0]
                // let domain = propsEmail.split("@")[1]
                // setEmail(propsEmail);
                // setEmail_name(name);
                // setEmail_domain(domain)
    
                // 
                async function fetchData() {
                    console.log('fetch Data');
                    const response = await axios.get(`https://www.1secmail.com/api/v1/?action=readMessage&login=${email_name}&domain=${email_domain}&id=${email_id}`);
                    console.log('respo', response);
                    if(response.status == 200 && response?.data) {
                        setData(response?.data?.textBody)
                    }
                }
                fetchData()
                
            }
        }
        catch(e) {
            console.log('error in useEffect[] ', e);
        }
    },[])

    return (
        <div>
            <div dangerouslySetInnerHTML={{ __html: data }} />
        </div>
    )
}

export default ContentPage
