import React, { useState, useEffect } from 'react'
import axios from 'axios';
import { Link } from 'react-router-dom'
import { setCookie, getCookie, removeCookie } from "../utils/cookie"

function EmailPage() {
    const [isLoading, setIsLoading] = useState(true)
    const [email, setEmail] = useState('No Email')
    const [data, setData] = useState([])
    const [value, setValue] = useState(0)

    const addNewMail = async () => {
        try {
            const response = await axios.get('https://www.1secmail.com/api/v1/?action=genRandomMailbox&count=1')
            console.log('res', response);
            if(response?.status == 200) {
                const newEmail = response.data[0]
                setEmail(newEmail)
                let newData = [...data]
                newData.push(newEmail)
                setData(newData)
                const emailCookie = getCookie('_emails_')
                console.log('emailCookie', emailCookie);
                if(emailCookie) {
                    console.log('emailCookie exist', emailCookie);
                }
                setCookie('_emails_', JSON.stringify(newData), 15)
            }
        }
        catch(e) {
            console.log('error in new mail ', e);
        }
    }

    useEffect(() => {
        console.log('uE EmailPage');
        async function fetchData() {
            console.log('fetchData');
            const emailCookie = getCookie('_emails_')
            console.log('emailCookie', emailCookie);
            if(emailCookie) {
                const parsedCookie = JSON.parse(emailCookie)
                if(parsedCookie.length > 0) {
                    setData(parsedCookie)
                    setEmail(parsedCookie[0])
                }
            }
        }
        fetchData()
    },[])

    const deleteMail = (mailIndx) => {
        try {
            console.log('deleteMail', mailIndx);
            let dataTemp = [...data]
            console.log('dataTemp', dataTemp);
            const removedEmail = dataTemp.splice(mailIndx, 1)
            console.log('removedEmail', removedEmail);
            console.log('dataTemp', dataTemp);
            setData(dataTemp)
            const jsonValue = JSON.stringify(dataTemp)
            setCookie('_emails_', jsonValue, 15)
            const length = dataTemp.length
            setValue(length)
            if(length > 0) {
                setEmail(dataTemp[length - 1])
            } 
            else {
                setEmail('No Email')
            } 
        } 
        catch(e) {
           console.log('error in deleteItemById', e);
        }
    }

    return (
        <>
            { (data?.length < 5) &&
                <div>
                    <button onClick={addNewMail}> {"New Mail"} </button>
                </div>
            }
            <div>
                {"current Email: " + email}
            </div>
            {
                (data?.length > 0) && data.map((mail, index) => {
                    return (
                        <div key={index}>
                            {index + " data " + mail}
                            <Link to={{ pathname: "/allEmail", state: { mail }}}>
                            <button> {"Check Mail"} </button>
                            </Link>
                            <button onClick={() =>{ deleteMail(index) }}> {"Delete Mail"} </button>
                        </div>
                    )
                }) 
            }
        </>
    )
}

export default EmailPage
