import { BrowserRouter as Router, Switch, Route } from 'react-router-dom'
import EmailPage  from './components/EmailPage'
import AllEmail  from './components/AllEmail'
import ContentPage  from './components/ContentPage'

function App() {
  return (
    <Router>
      <div className="App">
        <Switch>
          <Route path="/allEmail" exact component={AllEmail}></Route>
          <Route path="/contentPage" exact component={ContentPage}></Route>
          <Route path="/" exact component={EmailPage}></Route>
        </Switch>
      </div>
    </Router>
    
  );
}

export default App;
